


.contact-page {
    width: 100%;
    margin-top: 20px;
    
    
  
    ul {
      padding: 0;
      margin: 0;
      margin-left: 500px;
      margin-top: 800px;
      
  
      li {
        padding: 0;
        margin: 0;
        list-style: none;
        margin-bottom: 10px;
        opacity: 0;
        overflow: hidden;
        display: block;
        clear: both;
        position: relative;
        animation: fadeInUp 2s 2s;
        animation-fill-mode: forwards;
        top: -600px;
        
      }
  
      li.half {
        width: 49%;
        margin-left: -10%;
        float: left;
        clear: none;
  
        &:first-child {
          margin-left: 0;
        }
      }
    
  
    
    input[type='text'],
    input[type='email'] {
      width: 20%;
      border: 0;
      background: #fff;
      height: 60px;
      font-size: 42px;
      color: rgb(12, 9, 9);
      padding: 0 60px;
      box-sizing: border-box;
      
      border-radius: 20px;
    }
  }
  
    textarea {
      width: 60%;
      border: 0;
      background: #115173;
      height: 50px;
      font-size: 50px;
      color: #fff;
      padding: 20px;
      box-sizing: border-box;
      min-height: 100px;
    }
  
    .flat-button {
      color: #ffd700;
      font-size: 40px;
      letter-spacing: 3px;
      text-decoration: none;
      padding: 8px 10px;
      border: 1px solid #ffd700;
      float: left;
      border-radius: 4px;
      background: 0 0;
      text-transform: uppercase;
      float: left;
      text-align: center;
      margin-right: 100px;
      
    }
  }
  
  
  .container {
    position: relative;
    width: 100%;
    height: 100%;
    opacity: 0;
    animation: backInRight 1s 1.2s;
    animation-fill-mode: forwards;
  }
  
  .info-map {
    position: absolute;
    background: #000;
    top: 50px;
    right: 05%;
    z-index: 999999;
    width: 350px;
    padding: 20px;
    color: #fff;
    font-family: 'Helvetica';
    font-size: 30px;
    font-weight: 300;
    opacity: 0;
    animation: fadeIn 1s 1.5s;
    animation-fill-mode: forwards;
    border-radius: 20px;
  
    span {
      font-size: 30px;
  
      span {
        color: #ffd700;
      }
    }
  }